:root {
    --checkbox-size: 15px;
    --bg-color: #FFA69E;
    --checkbox-color: #e3a563;
    --hover-color: #FF686B;
    --tick-color: #84DCC6;
}





label {
    position: relative;
    top: 9px;
    display: inline-block;
    margin: 0 calc(15px * 0.25);
    margin: 0 calc(var(--checkbox-size) * 0.25);
    width: 15px;
    width: var(--checkbox-size);
    height: 15px;
    height: var(--checkbox-size);
    border: calc(15px * 0.125) solid #e3a563;
    border: calc(var(--checkbox-size) * 0.125) solid var(--checkbox-color);
    border-radius: 12.5%;
    transition: 400ms 100ms ease-out;

}

label:hover {
    border-color: #FF686B;
    border-color: var(--hover-color);
}

input[type="checkbox"] {
    position: absolute;
    left: -1000px;
}

.Checkbox_tick__3WTTn {
    position: relative;
    right: calc(15px * -0.5);
    right: calc(var(--checkbox-size) * -0.5);
    top: calc(15px * -0.25);
    top: calc(var(--checkbox-size) * -0.25);
    width: calc(15px * 0.25);
    width: calc(var(--checkbox-size) * 0.25);
    height: calc(15px * 0.75);
    height: calc(var(--checkbox-size) * 0.75);
    border-right: calc(15px * 0.25) solid #84DCC6;
    border-right: calc(var(--checkbox-size) * 0.25) solid var(--tick-color);
    border-bottom: calc(15px * 0.25) solid #84DCC6;
    border-bottom: calc(var(--checkbox-size) * 0.25) solid var(--tick-color);
    transform: rotate(45deg) scale(0);
    opacity: 0;
    transition: all 600ms cubic-bezier(0.175, 0.885, 0.32, 1.5);
}

.Checkbox_tick__3WTTn:before {
    content: '';
    position: absolute;
    left: calc(15px * -0.125);
    left: calc(var(--checkbox-size) * -0.125);
    bottom: calc(15px * -0.25);
    bottom: calc(var(--checkbox-size) * -0.25);
    border: calc(15px * 0.125) solid #84DCC6;
    border: calc(var(--checkbox-size) * 0.125) solid var(--tick-color);
    border-radius: 50%;
}

.Checkbox_tick__3WTTn:after {
    content: '';
    position: absolute;
    right: calc(15px * -0.25);
    right: calc(var(--checkbox-size) * -0.25);
    top: calc(15px * -0.125);
    top: calc(var(--checkbox-size) * -0.125);
    border: calc(15px * 0.125) solid #84DCC6;
    border: calc(var(--checkbox-size) * 0.125) solid var(--tick-color);
    border-radius: 50%;
}

input[type="checkbox"]:checked + label .Checkbox_tick__3WTTn {
    opacity: 1;
    transform: rotate(45deg) scale(1);
}

input[type="checkbox"]:focus + label {
    -webkit-animation-name: Checkbox_cb-pop__3dUhm;
    animation-name: Checkbox_cb-pop__3dUhm;
    -webkit-animation-duration: 400ms;
    animation-duration: 400ms;
    -webkit-animation-iteration-count: 1;
    animation-iteration-count: 1;
    -webkit-animation-timing-function: linear;
    animation-timing-function: linear;
}

@-webkit-keyframes Checkbox_cb-pop__3dUhm {
    0% {
        transform: scale(1);
    }
    33% {
        transform: scale(0.9);
    }
    66% {
        transform: scale(1.1);
    }
    100% {
        tranform: scale(1);
    }
}

@keyframes Checkbox_cb-pop__3dUhm {
    0% {
        transform: scale(1);
    }
    33% {
        transform: scale(0.9);
    }
    66% {
        transform: scale(1.1);
    }
    100% {
        tranform: scale(1);
    }
}

